.doubly-linked-list {
  width: 30px;
  border: 0;
  border-radius: 0;
  background-color: #ffbf00;
  color: transparent;
  margin: 0 0px;
  position: relative;
  max-height: 660px;
  color: #000000;
  font-size: 1em;
  width: 106px;
  height: 62px;
  display: inline-block;
  align-items: center;
  justify-content: center;
  border-right: 16px solid rgb(12, 53, 71);
  border-left: 16px solid rgb(12, 53, 71);
  margin-right: 50px;
  z-index: 1;
  transform-style: preserve-3d;
  border-radius: 10%;
  box-shadow: 5px 10px 10px #888888;
}

.p2 {
  font-size: 20px;
}


.doubly-linked-list:not(:first-child):after {
  content: "";
  height: 2px;
  background: black;
  width: 49px;
  position: absolute;
  right: 90px;
  top: 38%;
  animation-name: visitedAnimationlnr;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
}

.doubly-linked-list:not(:last-child):before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  top: 39.2%;
  border-style: solid;
  border-width: 7px 15px 7px 0px;
  border-color: transparent black transparent transparent;
  right: -30px;
  transform: translateY(-50%);
  animation-name: visitedAnimationtrir;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
}

.doubly-linked-list:not(:last-child) p:after {
  content: "";
  height: 2px;
  background: black;
  width: 59px;
  position: absolute;
  right: -59px;
  top: 64%;
  animation-name: visitedAnimationlnx;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
}

.doubly-linked-list:not(:last-child) p:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  top: 65.5%;
  border-style: solid;
  border-width: 7px 0 7px 15px;
  border-color: transparent transparent transparent black;
  right: -67px;
  transform: translateY(-50%);
  animation-name: visitedAnimationtrix;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
}

